function getStatusBadge(status: string) {
  const statusColors = {
    Active: "bg-green-100 text-green-800",
    Design: "bg-purple-100 text-purple-800",
    Idea: "bg-orange-100 text-orange-800"
  };
  
  return (
    <Badge className={statusColors[status as keyof typeof statusColors] || "bg-gray-100 text-gray-800"}>
      {status}
    </Badge>
  );
}

function getPriorityBadge(priority: string) {
  const priorityColors = {
    High: "bg-red-100 text-red-800",
    Medium: "bg-yellow-100 text-yellow-800",
    Low: "bg-blue-100 text-blue-800"
  };
  
  return (
    <Badge className={priorityColors[priority as keyof typeof priorityColors] || "bg-gray-100 text-gray-800"}>
      {priority}
    </Badge>
  );
}

function TeamAvatars({ people }: { people: Array<{ name: string; avatar: string }> }) {
  return (
    <div className="flex -space-x-2">
      {people.slice(0, 3).map((person, index) => (
        <Avatar key={index} className="w-8 h-8 border-2 border-white">
          <AvatarFallback className="text-xs">
            {person.name.charAt(0)}
          </AvatarFallback>
        </Avatar>
      ))}
      {people.length > 3 && (
        <div className="w-8 h-8 rounded-full bg-gray-200 border-2 border-white flex items-center justify-center">
          <span className="text-xs text-gray-600">+{people.length - 3}</span>
        </div>
      )}
    </div>
  );
}

